.errorContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  .errorImg {
    width: 480px;
    height: 204px;
    object-fit: contain;
    margin-bottom: 80px;
  }
  .errorBtn {
    width: 175px;
    height: 48px;
    padding: 12px 41px 13px 43px;
    border-radius: 4px;
    border: solid 1px #333333;
  }
}
